.skills-container {
    position: relative;
    padding-bottom: 0rem;
    padding-top: 12rem;
}

.skills-container::after,
.skills-container::before {
    content: ' ';
    width: 28rem;
    height: 28rem;
    border-radius: 50rem;
    background: #181233d0;
    position: absolute;
    z-index: -1;
    filter: blur(200px);
}

.skills-container::after {
    top: -3rem;
    left: -5rem;
}

.skills-container::before {
    background: #0f1d33;
    bottom: 0rem;
    right: 0rem;
}

.skills-container h5 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 3.5rem;
}

.skills-content {
    display: flex;
    align-items: flex-start;
    gap: 3rem;
}

.skills {
    flex: 1;
    display: grid;
    grid-gap: 3rem;
    grid-template-columns: repeat(2 , 1fr);
}

.skills img {
    width: 100px;
}

.skills-info {
    flex: 1;
}

@media (max-width:1025px) {
    .skills {
        padding-left: 1rem;
    }

    .skills,
    .skills-content {
        padding: 3rem;
    }
}

@media (max-width:768px) {
    .skills-content {
        flex-direction: column-reverse;
        gap: 3rem;
    }

    .skills,
    .skills-info {
        width: 100%;
    }

    .skills-container h5 {
        font-size: 1.3rem;
        margin-bottom: 2rem;
    }

    .skills-container::after,
    .skills-container::before {
        height: 18rem;
        width: 18rem;
        filter: blur(150px);
    }

}

@media (max-width:600px) {
    .skills-container {
        padding: 0;
    }
}


@keyframes appear {
    from {
      opacity: 0;
      scale: 0.5;
    }
    to {
      opacity: 1;
      scale: 1;
    }
  }
  
  .block {
    animation: appear linear 5s;
    animation-timeline: view();
    animation-range: entry 0;
  }

