.nav-content-main {
    max-width: 1000px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 0;
    margin: 0 auto;
}

.contact-btn-footer {
    font-size: 0.9rem;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    background: linear-gradient(90deg , #3bb1d4 0%, #3766ca 100%);
    padding: 0.5rem 2.5rem;
    border: none;
    outline: 1.5px solid transparent;
    border-radius: 0.2rem;
    cursor: pointer;
    transition: all 0.1s ease;
    margin-left: 20px;
    border-radius: 50px;
}

.logo-footer {
    width: 4rem;
    height: auto;
    margin-left: 10px;
}

.nav-content-main ul {
    display: flex;
    align-items: center;
    gap: 0.3rem;
    list-style: none;
}

.nav-content-main li {
    margin: 0 1.5rem;
}
.menu-item-footer {
    text-decoration: none;
    font-size: 1.1rem;
    font-weight: 500;
    color: #fff;
    position: relative;
    cursor: pointer;
}

.menu-item-last-footer {
    text-decoration: none;
    font-size: 1.1rem;
    font-weight: 500;
    color: #fff;
    position: relative;
    cursor: pointer;
}



.float {
    width: 1200px;
    border-radius: 25px;
    height: 160px;
    background-color: #1b2435;
    left: 0px;
    right: 0px;
    bottom: 8px;
    max-width: calc(
      100vw - 8px * 2
    );
    margin-inline: auto;
    margin-bottom: 100px;
}

.details-p {
    margin-left: 110px;
}

.icon-github {
    margin-left: 620px;
    width: 2rem;
}

.content {
    display: flex;
    margin-top: 25px;
}

@media (max-width:1200px) {
    .float {
        width: 900px;
    }

    .menu-item-footer , .menu-item-last-footer {
        font-size: 16px;
    }

    .logo-footer {
        font-size: 25px;
        margin-left: 15px;
    }

    .contact-btn-footer {
        font-size: 16px;
        padding: 6px 12px;
    }

    .nav-content-main {
        max-width: 850px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 1rem 0;
        margin: 0 auto;
    }

    .detail-p {
        font-size: 13px;
    }
}

