.about-container {
    position: relative;
    padding-bottom: 6rem;
    padding-top: 12rem;
}

.about-container::after,
.about-container::before {
    content: ' ';
    width: 20rem;
    height: 20rem;
    border-radius: 50rem;
    background: #082146;
    position: absolute;
    z-index: -1;
    filter: blur(150px);
}

.about-container::after {
    top: -3rem;
    left: -5rem;
}

.about-container::before {
    background: #17294b;
    bottom: 0rem;
    right: 0rem;
}

.heading {
    font-size: 1.5rem;
    margin-bottom: 70px;
    font-weight: 600;
}


.about__container {
    display: flex;
    grid-template-columns: 35% 50%;
    gap: 15%;
}

.My-Image {
    width: 400px;
    height: auto;
    border-radius: 30rem;
}
.about__me-image {
    border-radius: 2rem;
}


.about__cards {
    display: grid;
    grid-template-columns: repeat(3 ,1fr);
    gap: 1.5rem;
}

.about__card {
    border: 2px solid #1d89e0;
    border-radius: 1rem;
    padding: 2rem;
    text-align: center;
    background: #003c8026;
    font-size: 1.2rem;
    margin-bottom: 50px;
}

.about__card i{
    font-size: 1rem;
}

.btn2 {
    background: rgba(96, 96, 213, 0.048);
    border: 2.0px solid #1d89e0;
    padding: 0.61rem 4.2rem 0.61rem 4.2rem;
    border-radius: 7px;
    margin-top: 30px;
    transition: all ease 0.09s;
}

.btn2:hover {
    background: #1d89e0;
}

.btn2:active {
    transform: scale(0.97);
}

.btn2 a{
    text-decoration: none;
    color: aliceblue;
    font-size: 0.9rem;
}


p {
    color: #c9c9c9;
}

/* ========================== RESPONSIVE DESIGN ================================= */

@media screen and (max-width: 1090px) {
    .about__container {
        grid-template-columns: 1fr;
        gap: 0;
    }

    .My-Image {
        display: none;
    }

    .about-content p{
        margin: 1rem 0 1.5rem;
    }
}

@media screen and (max-width: 600px) {
    .about__card {
        font-size: 0.8rem;
        padding: 1.5rem;
    }

    .about-content p{
        margin: 1rem 0 1.5rem;
        font-size: 0.8rem;
    }

    .btn {
        width: 100%;
    }
}

@media screen and (max-width: 425px) {
    .about__card {
        font-size: 0.8rem;
        padding: 1.3rem;
    }

    .about-content p{
        margin: 1rem 0 1.5rem;
    }

    p {
        font-size: 0.8rem;
    }

    .btn {
        width: 100%;
    }
}

@keyframes appear {
    from {
      opacity: 0;
      scale: 0.5;
    }
    to {
      opacity: 1;
      scale: 1;
    }
  }
  
  .block {
    animation: appear linear 5s;
    animation-timeline: view();
    animation-range: entry 0;
  }


